import React from 'react';
import '../assets/scss/pages/_fourteenth-page.scss';

import PageFade from '../components/page-fade';
import Downlaod from '../assets/img/download.svg';
import Checked from '../assets/img/checked.svg';
import Google from '../assets/img/google.svg';
import Apple from '../assets/img/apple.svg';

const FourteenthPage = () => {
  return (
    <PageFade current={13}>
      <section id="fourteenth-page">
        <div className="main container h-100 px-64">
          <div className="centered-box z-1 full-width text-center">
            <h1 className="text-primary">Your Policy is Ready!</h1>
            <button className="btn btn-white">Download It Now <img src={Downlaod} alt="Downlaod" /></button>
            <div className="emailed"><img src={Checked} alt="Checked" /> We also emailed it</div>
            <div className="appstore">
              <p>Download our app</p>
              <p>We will notify you if something changes in your flight</p>
              <div className="divider"></div>
              <div className="appstore__items">
                <img src={Google} alt="Google Appstore" />
                <img src={Apple} alt="Apple Appstore" />
              </div>
            </div>
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default FourteenthPage