import React, { useCallback, useState, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../assets/scss/pages/_fifth-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import Scrollbar from '../components/scrollbar';
import ValidTextInput from '../components/form/ValidTextInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'
import greyLeft from '../assets/img/fi_arrow-left_grey.svg';

const takeoffValidator = [
  { func: Validators.isRequired, msg: 'Takeoff Flight is required!'},
  { func: Validators.minLength(3), msg: 'Takeoff Flight must have more than 3 letters!'}
];
const returnValidator = [
  { func: Validators.isRequired, msg: 'Return Flight is required!'},
  { func: Validators.minLength(3), msg: 'Return Flight must have more than 3 letters!'}
];

const FifthPage = () => {
  const [takeoff, setTakeoff] = useState('');
  const [retFlight, setRetFlight] = useState('');
  const {flightNumbers} = useSelector(store => store.user);
  const dispatch = useDispatch();

  const takeoffMsg = useValidator(takeoff, takeoffValidator);
  const retFlightMsg = useValidator(retFlight, returnValidator);
  
  const onAddItem = useCallback( e => {
    e.preventDefault();
    flightNumbers.push({first: takeoff, second: retFlight});
    dispatch({
      type: Actions.CHANGE_FLIGHTS,
      data: flightNumbers
    })
    setTakeoff('');
    setRetFlight('');
  }, [flightNumbers, dispatch, takeoff, retFlight]);

  const onRemove = useCallback(e => {
    e.preventDefault();
    setTakeoff('');
    setRetFlight('')
  }, []);

  const done = useMemo(() => {
    return !takeoffMsg && !retFlightMsg && takeoff.length > 0 && retFlight.length > 0
  }, [takeoffMsg, retFlightMsg, takeoff, retFlight]);

  const submitable = !!flightNumbers.length > 0;

  return(
    <PageFade current={4}>
      <section id="fifth-page">
        <div className="container h-100 px-64 footer-border bg-white">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Enter Your<br/>Flight Numbers</h2>
            {flightNumbers.length > 0 && 
              <Scrollbar data={flightNumbers} title="flight"/>}
            <div className="form-groups">
              <form className="flight-box" id="flight-0">
                <div className="flight-box__wrappper">
                  <ValidTextInput id="flight-takeoff"
                    placeholder="Takeoff Flight"
                    value={takeoff}
                    setValue={setTakeoff}
                    error={!submitable && takeoffMsg}
                  />
                </div>
                <div className="flight-box__wrapper ml-tw-24 mt-tw-20">
                  <ValidTextInput id="flight-takeoff"
                    placeholder="Return Flight"                                  
                    value={retFlight}
                    setValue={setRetFlight}
                    error={!submitable && retFlightMsg}
                  />
                  <span className="flight-box__wrapper__before">
                    Covid Instrunction to Madrid <img src={greyLeft} alt="Grey Left Arrow"/>
                  </span>
                </div>
                <div className="btn btn-add btn-flight">
                  {!done && <span className="status">+</span>}
                  <button className="btn-add" disabled={!done} onClick={onAddItem}>{done ? 'Done' : 'Add a flight'}</button>
                  {done && <span className="btn_done" onClick={onRemove}>+</span>}
                </div>
              </form>
            </div>
            <NextButton label="Next" disabled={!submitable} where={5} current={4} />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default FifthPage;