import React from 'react';
import { useDispatch } from 'react-redux';
import '../assets/scss/pages/_eighth-page.scss';

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import * as Actions from '../store/action'

const EighthPage = () => {
  const dispatch = useDispatch();  
  return(
    <PageFade current={7}>
      <section id="eighth-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Will you travel with<br/>someone?</h2>
            <div className="button-group">
              <button className="btn btn-white" 
                onClick={() => dispatch({
                  type: Actions.CHANGE_PAGENAME,
                  data: {
                    where: 9,
                    current: 7
                   }
                })}
              >
                NO
              </button>
              <NextButton label="YES" where={8} current={7} />
            </div>
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default EighthPage;