import React, { useState } from 'react'

const ValidTextInput = props => {
    const { label, id, value, setValue, error, pClass = '', ...rest } = props
    const [focus, setFocus] = useState(false);

    const valueChange = React.useCallback(e => {
        setValue(e.target.value)
    }, [setValue])

    const cls = React.useMemo(() => {
        return error ? 'form-control border border-danger' : 'form-control'
    }, [error])

    return (
        <div className={`${pClass} form-group${focus? ' focus' : ''}`}>
            <input
                type='text'
                className={cls}
                aria-describedby={`${id}-help`}
                id={id}
                value={value}
                onChange={valueChange}
                onFocus={() =>  setFocus(true)}
                onBlur={() => setFocus(false)}
                {...rest}
            />
            <small
                id={`${id}-help`}
                className='form-text text-danger'
                style={{ visibility: error ? 'visible' : 'hidden' }}
            >
                {error}
            </small>
        </div>
    )
}

export default ValidTextInput
