import React, { useState, useMemo } from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_eleventh-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import ValidTextInput from '../components/form/ValidTextInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'

const emailValidator = [
  { func: Validators.isRequired, msg: 'Email is required!'},
  { func: Validators.isEmail, msg: 'Please input email format!'},
];

const EleventhPage = () => {
  const {emailAdd} = useSelector(store => store.user)
  const [email, setEmail] = useState( emailAdd || '' );

  const emailMsg = useValidator(email, emailValidator);

  const submitable = useMemo(() => {
    return !emailMsg && email.length > 0
  }, [emailMsg, email]);

  return (
    <PageFade current={10}>
      <section id="eleventh-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h3>Just few more questions.</h3>
            <h2 className="text-primary">What is your Email?</h2>
            <form>
              <ValidTextInput id="email"
                placeholder="example@email.com"
                value={email}
                setValue={setEmail}
                error={emailMsg}
              />
            </form>
            <NextButton label="Next"
              disabled={!submitable}
              where={11}
              current={10}
              data={{
                type: Actions.CHANGE_EMAIL,
                data: email
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default EleventhPage;