import React, {useState, useMemo} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_second-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import ValidTextInput from '../components/form/ValidTextInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'

const firstNameValidator = [
  { func: Validators.isRequired, msg: 'First Name is required!'},
  { func: Validators.minLength(2), msg: 'First Name must have more than 2 letters!'}
];
const lastNameValidator = [
  { func: Validators.isRequired, msg: 'Last Name is required!'},
  { func: Validators.minLength(2), msg: 'Last Name must have more than 2 letters!'}
];

const SecondPage = () => {
  const {fullname} = useSelector(store => store.user)
  const [firstname, setFirstname] = useState( fullname.split(' ')[0] || '' );
  const [lastname, setLastname] = useState( fullname.split(' ')[1] || '' );

  const firstnameMsg = useValidator(firstname, firstNameValidator);
  const lastnameMsg = useValidator(lastname, lastNameValidator);

  const submitable = useMemo(() => {
    return !firstnameMsg && !lastnameMsg && firstname.length > 0 && lastname.length > 0
  }, [firstnameMsg, lastnameMsg, firstname, lastname]);

  return (
    <PageFade current={1}>
      <section id="second-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">We will set up a quote in no time.<br/> Let's start!</h2>
            <form>
              <ValidTextInput placeholder="First Name"
                id="first-name"
                value={firstname}
                setValue={setFirstname}
                error={firstnameMsg}
              />
              <ValidTextInput placeholder="Last Name"
                id="last-name"
                pClass="ml-tw-24 mt-tw-20"
                value={lastname}
                setValue={setLastname}
                error={lastnameMsg}
              />
            </form>
            <NextButton label="Next"
              disabled={!submitable}
              where={2}
              current={1}
              data={{
                type: Actions.ADD_FULLNAME,
                data: firstname + ' ' + lastname
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default SecondPage;