import React, {useState, useEffect, useCallback, useRef} from 'react';
import '../../assets/scss/components/select-dropdown.scss'
import '../../assets/scss/components/scrollbar.scss';

import DropDownSVG from '../../assets/img/dropdown.svg'

const SelectDropdown = props => {
  const { id, value, setValue, pClass = '', data, placeholder, disNum = -1} = props;
  const ref = useRef();
  // useOutsideAlerter(ref);
  const [showDropdown, setShowDropdown] = useState(false);
  const [currentVal, setCurrentVal] = useState(value);
  
  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShowDropdown(false);
      }
    }
    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [ref]);

  const toggleDropdown = useCallback(() => {
    setShowDropdown(!showDropdown);
  }, [showDropdown])

  const setSelected = useCallback( (event, i) => {
    if (i <= disNum)
      return;
    const selectedOption = event.target.getAttribute('value')
    setShowDropdown(false);
    setCurrentVal(selectedOption);

    if (setValue) {
      setValue(selectedOption)
    }
  }, [setValue, disNum]);

  return (
    <div className={`dropdown ${pClass}`}
         ref={ref}
         id={id} >
      <div className={`${showDropdown ? 'show ' : ''}dropdown__option`} onClick={()=>toggleDropdown()}>
        <span className="drowndown__option__label">{currentVal.length > 0 ? currentVal : placeholder }</span>
        <img className="drowndown__option__icon" src={DropDownSVG} alt="DropDown Arrow" />
      </div>
      <ul className={`${showDropdown ? 'show ' : ''}dropdown__list scrollbar`}>
        {
          data.map((item, index) => (
            <li
              className={index > disNum ? 'dropdown__list__item' : 'dropdown__list__item disable'}
              key={index}
              value={item}
              onClick={ event => setSelected(event, index)}
            >
              {item}
            </li>
          ))
        }
      </ul>
    </div>
  )
}

export default SelectDropdown