import React, {useState, useCallback} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../assets/scss/pages/_tenth-page.scss';

import ArrowLeft from '../assets/img/fi_arrow-left.svg';
import PageFade from '../components/page-fade';
import TravelCard from '../components/travel-card';
import * as Actions from '../store/action';

const TenthPage = () => {
  const {wholeTrip} = useSelector(store => store.user);
  const dispatch = useDispatch();
  const [tripInteruption, setTripInteruption] = useState(wholeTrip.tripInteraption.value);
  const [tripInteruptionChecked, setTripInteruptionChecked] = useState(wholeTrip.tripInteraption.checked);

  const [medicalExpenses, setMedicalExpenses] = useState(wholeTrip.medicalExpenses.value);
  const [medicalExpensesChecked, setMedicalExpensesChecked] = useState(wholeTrip.medicalExpenses.checked);

  const [baggageLost, setBaggageLost] = useState(wholeTrip.baggageLost.value);
  const [baggageLostChecked, setBaggageLostChecked] = useState(wholeTrip.baggageLost.checked);

  const [tripCancellation, setTripCancellation] = useState(wholeTrip.tripCancellation.value);
  const [tripCancellationChecked, setTripCancellationChecked] = useState(wholeTrip.tripCancellation.checked);

  const price = (tripInteruptionChecked  ? tripInteruption  : 0) +
                (medicalExpensesChecked  ? medicalExpenses  : 0) +
                (baggageLostChecked      ? baggageLost      : 0) +
                (tripCancellationChecked ? tripCancellation : 0);

  const onNext = useCallback(() => {
    dispatch({
      type: Actions.CHANGE_WHOLETRIP,
      data: {
        tripInteraption: {
          value: tripInteruption,
          checked: tripInteruptionChecked
        },
        medicalExpenses: {
          value: medicalExpenses,
          checked: medicalExpensesChecked
        },
        baggageLost: {
          value: baggageLost,
          checked: baggageLostChecked
        },
        tripCancellation: {
          value: tripCancellation,
          checked: tripCancellationChecked
        }
      }
    });
    dispatch({
      type: Actions.CHANGE_PAGENAME,
       data: { where: 10, current: 9 }
    })
  }, [dispatch, baggageLost, baggageLostChecked, medicalExpenses, medicalExpensesChecked, tripCancellation, tripCancellationChecked, tripInteruption, tripInteruptionChecked])

  return (
    <PageFade current={9}>
      <section id="tenth-page">
        <div className="main container h-100 px-64">
        <div className="z-1 full-width text-center">
          <h2 className="text-primary">Zenner Travel Insurance</h2>
          <button className="btn btn-trip" onClick={onNext}>
            <div className="btn-trip__wrapper">
              <span>$3 a Day</span>
              <span>${price} for the whole trip</span>
            </div>
            <img src={ArrowLeft} alt="Left Arrow"/>
          </button>
          <div className="travel-cards">
            <TravelCard title="Includes trip interuption"
              desc="Detect flight delays and cancellations before anyone else and secure a resolution in seconds"
              value={tripInteruption}
              setValue={setTripInteruption}
              checking={tripInteruptionChecked}
              setChecking={setTripInteruptionChecked}
              id="trip-interuption"
            />
             <TravelCard title="Includes medical expenses up to"
              desc="Detect flight delays and cancellations before anyone else and secure a resolution in seconds"
              value={medicalExpenses}
              setValue={setMedicalExpenses}
              checking={medicalExpensesChecked}
              setChecking={setMedicalExpensesChecked}
              id="medical-expenses"
            />
             <TravelCard title="Includes baggage lost/damage"
              desc="Detect flight delays and cancellations before anyone else and secure a resolution in seconds"
              value={baggageLost}
              setValue={setBaggageLost}
              checking={baggageLostChecked}
              setChecking={setBaggageLostChecked}
              id="baggage-lost"
            />
             <TravelCard title="Adding trip cancellation"
              desc="Detect flight delays and cancellations before anyone else and secure a resolution in seconds"
              value={tripCancellation}
              setValue={setTripCancellation}
              checking={tripCancellationChecked}
              setChecking={setTripCancellationChecked}
              id="trip-cancellation"
            />
          </div>
          <div className="travel-policy disable">
            <span className="travel-policy__des">We will be with you all along with your flight</span>
            <div className="travel-policy__methods">
              <span className="travel-policy__methods__item">Notify you on extreme weather</span>
              <span className="travel-policy__methods__item">We will be here for you 24/7</span>
              <span className="travel-policy__methods__item">Pre-charged credit card</span>
              <span className="travel-policy__methods__item">Add one more value...</span>
            </div>
            <div className="travel-policy__btn">
              <button className="btn" disabled={true}><span>I Want This Policy</span><img src={ArrowLeft} alt="Left Arrow"/></button>
            </div>
          </div>
        </div>
      </div>
      </section>
    </PageFade>
  )
}

export default TenthPage;