import React from 'react';
import '../assets/scss/pages/_first-page.scss';

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';

const FirstPage = () => {
  return (
    <PageFade current={0}>
      <section id="first-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 full-width text-center">
            <h1 className="text-primary">Remove Stress From Traveling</h1>
            <h3 className="text-secondary">66% of customer use our assistance</h3>
            <NextButton label="GET A QUOTE" where={1} current={0}/>
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default FirstPage