import React, { useCallback }  from 'react';

const TravelCard = props => {
  const {title, desc, checking, value, setValue, setChecking, id} = props;

  const onPriceMinus = useCallback(() => {
    if (value - 1 > 0)
      setValue(value - 1);
  }, [setValue, value]);

  const onPricePlus = useCallback(() => {
    if (value + 1 <= 50)
    setValue(value + 1);
  }, [setValue, value]);

  const onToggle = useCallback(e => {
    setChecking(e.target.checked);
  }, [setChecking]);

  return (
    <div className={`travel-card${checking ? '' : ' disable'}`}>
      <span>{title}</span>
      <span>{desc}</span>
      <span className="divider"></span>
      <div className="travel-price">
        <button className="travel-price__btn" disabled={!checking} onClick={onPriceMinus}>-</button>
        <div className="travel-price__box">
          <div>up to</div>
          <div>$<span>{value}</span></div>
        </div>
        <button className="travel-price__btn" disabled={!checking} onClick={onPricePlus}>+</button>
      </div>
      <div className="toggle-btn">
        <input type="checkbox" className="toggle-switch" id={id} onChange={event => onToggle(event)} checked={checking} />
        <label htmlFor={id}><span className="sw"></span></label>
      </div>
    </div>
  )
}

export default TravelCard;