import React, {useState, useMemo} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_twelveth-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import PhoneInput from '../components/form/PhoneInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'

const phoneValidator = [
  { func: Validators.isRequired, msg: 'Phone is required!'},
];

const TwelvethPage = () => {
  const {phoneNum} = useSelector(store => store.user)
  const [phone, setPhone] = useState( phoneNum || '' );

  const phoneMsg = useValidator(phone, phoneValidator);

  const submitable = useMemo(() => {
    return !phoneMsg && phone.length > 0
  }, [phoneMsg, phone]);

  return (
    <PageFade current={11}>
      <section id="eleventh-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">What is your phone number?</h2>
            <form>
              <PhoneInput id="phone"
                placeholder="example@phone.com"
                value={phone}
                setPhone={setPhone}
                error={phoneMsg}
              />
            </form>
            <NextButton label="Next"
              disabled={!submitable}
              where={12}
              current={11}
              data={{
                type: Actions.CHANGE_PHONE,
                data: phone
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default TwelvethPage;