import React from 'react';
import { useDispatch } from 'react-redux';
import '../assets/scss/pages/_fourth-page.scss';

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import * as Actions from '../store/action'

const FourthPage = () => {
  const dispatch = useDispatch();
  return(
    <PageFade current={3}>
      <section id="fourth-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Do you know your<br/>flight number?</h2>
            <p className="text-secondary">Why are we asking?</p>
            <div className="button-group">
              <button className="btn btn-white" 
                onClick={() => dispatch({
                  type: Actions.CHANGE_PAGENAME,
                  data: {
                    where: 6,
                    current: 3
                   }
                })}
              >
                NO
              </button>
              <NextButton label="YES" where={4} current={3} />
            </div>
            <div className="my-alert">
              Because you are mostly offline in upwork, it is difficult to have responsive communication.
              If you don't mind, I would like to have a discussion through slack for responsive communication. 
              My email is nikola.pavloon@gmail.com. 
              Please invite me on slack.
              Thanks.
            </div>
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default FourthPage;