import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_thirteenth-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import CreditCard from '../components/credit-card';
import * as Actions from '../store/action'

const ThirteenthPage = () => {
  const {cardData} = useSelector(store => store.user);
  const [card, setCard] = useState(cardData);

  const submitable = card.cardNumber.length === 16 &&
                     card.cardName.length > 4 &&
                     card.cardCvv.length === 3 &&
                     card.cardMonth !== 0 &&
                     card.cardYear !== 0 &&
                     card.cardType !== '';

  return (
    <PageFade current={12}>
      <section id="thirteenth-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Lastly, the payment method</h2>
            <form>
              <CreditCard
                setValue={setCard}
                value={card}
              />
            </form>
            <NextButton label="Pay 2$"
              disabled={!submitable}
              where={13}
              current={12}
              data={{
                type: Actions.CHANGE_CARD,
                data: card
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default ThirteenthPage;