import React, {useCallback, useState, useMemo} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_third-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import SelectDropdown from '../components/form/selectDropDown';
import * as Actions from '../store/action';

const monArr = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'];

const ThirdPage = () => {
  const {fullname, birthday} = useSelector(store=>store.user);
  const [dayVal, setDayVal] = useState(birthday.day || '');
  const [monVal, setmonVal] = useState(birthday.month || '');
  const [yearVal, setYearVal] = useState(birthday.year || '');

  const submitable = useMemo(() => {
    return dayVal.length > 0 && monVal.length > 0 && yearVal.length > 0
  }, [dayVal, monVal, yearVal]);

  const day = useCallback(()=> {
    const array = []
    for (let i = 1; i <= 31; i++) {
      array.push(i);
    }
    return array;
  }, []);

  const month = useCallback(() => {
    const array = [];
    for (let i = 1; i <= 12; i++) {
      array.push(monArr[i - 1]);
    }
    return array;
  }, []);

  const year = useCallback(() => {
    const today = new Date();
    const currentYear = today.getFullYear();
    const array = [];
    for (let i = 0; i < 120; i++) {
      array.push(currentYear - i);
    }
    return array;
  }, []);

  return (
    <PageFade current={2}>
      <section id="third-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Hi {fullname}!<br/>When is your birthday?</h2>
            <form>
              <SelectDropdown key="day"
                id="day"
                pClass="input-day"
                data={day()}
                placeholder='DD'
                value={dayVal}
                setValue={setDayVal}
              />
              <SelectDropdown key="month"
                id="month"
                pClass="input-month ml-tw-24 mt-tw-20"
                data={month()}
                placeholder='MM'
                value={monVal}
                setValue={setmonVal}
              />
              <SelectDropdown key="year"
                id="year"
                pClass="input-year ml-tw-24 mt-tw-20"
                data={year()}
                placeholder='YYYY'
                value={yearVal}
                setValue={setYearVal}
              />
            </form>
            <NextButton label="Next"
              disabled={!submitable}
              where={3}
              current={2}
              data={{
                type: Actions.CHANGE_BIRTHDAY,
                data: {
                  day: dayVal,
                  month: monVal,
                  year: yearVal
                }
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default ThirdPage;