import React, {useCallback} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../assets/scss/style.scss';

import logo from '../assets/img/logo.svg';
import prevIcon from '../assets/img/prev_arrow.svg';
import * as Actions from '../store/action'

const Layout = props => {
  const dispatch = useDispatch();
  const {pageID} = useSelector(store => store.page)

  const goHome = useCallback(() => {
    dispatch({
      type: Actions.RESET_PAGENAME,
    })
  }, [dispatch]);
  return (
    <React.Fragment>
      <header data-role="header">
        <div className="menu cross px-64">
          <span className="logo" onClick={goHome}>
            <img src={logo} alt="Zenner"/>
          </span>
        </div>    
      </header>
      {props.children}
      { (pageID > 1 &&
        pageID <12 )  &&
        <button className="prev"
        onClick={() => dispatch({
          type: Actions.PREV_PAGENAME
          })}
        >
          <img src={prevIcon} alt="PrevSVG" />
          {pageID === 10 && <span>Just few more questions.</span>}
        </button>}
    </React.Fragment>
  )
}

export default Layout;