import React, {useState, useMemo} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_seventh-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import ValidTextInput from '../components/form/ValidTextInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'

const cityNameValidator = [
  { func: Validators.isRequired, msg: 'City Name is required!'},
];

const SeventhPage = () => {
  const {city} = useSelector(store => store.user)
  const [cityName, setCityName] = useState( city || '' );

  const cityNameMsg = useValidator(cityName, cityNameValidator);

  const submitable = useMemo(() => {
    return !cityNameMsg && cityName.length > 0
  }, [cityNameMsg, cityName]);

  return (
    <PageFade current={6}>
      <section id="seventh-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">We will set up a quote in no time.<br/>Let's start!</h2>
            <form>
              <ValidTextInput
                id="city"
                placeholder="City"
                value={cityName}
                setValue={setCityName}
                error={cityNameMsg}
              />
            </form>
            <NextButton
              label="Next"
              disabled={!submitable}
              where={7}
              current={6}
              data={{
                type: Actions.CHANGE_CITY,
                data: cityName
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default SeventhPage;