import React, { useCallback } from 'react';
import '../assets/scss/components/scrollbar.scss';

import { useDispatch } from 'react-redux';
import * as Actions from '../store/action'

const Scrollbar = props => {
  const {data, title} = props;
  const dispatch = useDispatch();

  const onRemoveItem = useCallback( index => {
    data.splice(index, 1);
    dispatch({
      type: Actions.CHANGE_FLIGHTS,
      data
    })
  }, [data, dispatch]);
  
  return (
    <div className="flights lists scrollbar">
      {data.map((item, index) => 
        <div className="lists__item" key={index}>
          <span>{item.first}</span>
          <span>{item.second}</span>
          <span className="btn-remove" onClick={() => onRemoveItem(index)} >
            <span className="status">-</span>
            <span className="description">Remove {title}</span>
          </span>
        </div>
      )}
    </div>
  )
}

export default Scrollbar;