import * as Actions from '../action'
const initialState = {
  fullname: '',
  birthday: {
    day: '',
    month: '',
    year: ''
  },
  flightNumbers: [],
  dateList: {
    start: '',
    end: ''
  },
  city: '',
  friends: [],
  wholeTrip: {
    tripInteraption: {
      value: 25,
      checked: true
    },
    medicalExpenses: {
      value: 25,
      checked: true
    },
    baggageLost: {
      value: 25,
      checked: true
    },
    tripCancellation: {
      value: 25,
      checked: false
    }
  },
  emailAdd: '',
  phoneNum: '',
  cardData: {
    cardNumber: '',
    cardName: '',
    cardCvv: '',
    cardMonth: 0,
    cardYear: 0,
    cardType: ''
  }
}

const user = (state = initialState, action) => { 
  switch (action.type) {
    case Actions.ADD_FULLNAME:
      return {
        ...state,
        fullname: action.data,
      }
    case Actions.CHANGE_BIRTHDAY:
      return {
        ...state,
        birthday: action.data,
      }
    case Actions.CHANGE_FLIGHTS:
      return {
        ...state,
        flightNumbers: action.data,
      }
    case Actions.CHANGE_DATE:
      return {
        ...state,
        dateList: action.data
      }
    case Actions.CHANGE_CITY:
      return {
        ...state,
        city: action.data
      }
    case Actions.CHANGE_WHOLETRIP:
      return {
        ...state,
        wholeTrip: action.data
      }
    case Actions.CHANGE_EMAIL:
      return {
        ...state,
        emailAdd: action.data
      }
    case Actions.CHANGE_PHONE:
      return {
        ...state,
        phoneNum: action.data
      }
    case Actions.CHANGE_CARD:
      return {
        ...state,
        cardData: action.data
      }
    default: return state;
  }
}

export default user;