import * as Actions from '../action'

const initialState = {
  pageID: 0,
  prev: []
}

const auth = (state = initialState, action) => {
  switch (action.type) {
    case Actions.CHANGE_PAGENAME:
      const array = state.prev;
      array.push(action.data.current);
      return {
        pageID: action.data.where,
        prev: array
      }
    case Actions.PREV_PAGENAME:
      const pageID = state.prev[state.prev.length - 1];
      const prevArray = state.prev
      prevArray.splice(prevArray.length - 1, 1);
      return {
        pageID,
        prev: prevArray
      }
    case Actions.RESET_PAGENAME: return initialState;
    default: return state;
  }
}

export default auth;