import React, { useCallback, useState, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import '../assets/scss/pages/_nineth-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import Scrollbar from '../components/scrollbar';
import ValidTextInput from '../components/form/ValidTextInput';
import { useValidator } from '../components/hooks/validator';
import * as Validators from '../helper/validators';
import * as Actions from '../store/action'

const friendValidator = [
  { func: Validators.isRequired, msg: 'Companion’s Name is required!'},
];

const ageValidator = [
  { func: Validators.isRequired, msg: 'Age is required!'},
  { func: Validators.isNumber, msg: 'Age must be number!'},
];

const NinethPage = () => {
  const {friends} = useSelector(store => store.user);
  const dispatch = useDispatch();

  const [friend, setFriend] = useState('');
  const [age, setAge] = useState('');

  const friendMsg = useValidator(friend, friendValidator);
  const ageMsg = useValidator(age, ageValidator);
  
  const onAddItem = useCallback( e => {
    e.preventDefault();
    friends.push({first: friend, second: age});
    dispatch({
      type: Actions.CHANGE_FRIENDS,
      data: friends
    })
    setFriend('');
    setAge('');
  }, [friends, dispatch, friend, age]);

  const onOnlyMe = useCallback(() => {
    dispatch({
      type: Actions.CHANGE_FRIENDS,
      data: []
    });
    console.log('test');
    dispatch({
      type: Actions.CHANGE_PAGENAME,
      data: {
        where: 9,
        current: 8
       }
    })
  }, [dispatch]);

  const done = useMemo(() => {
    return !friendMsg && !ageMsg && friend.length > 0 && age.length > 0
  }, [friendMsg, ageMsg, friend, age]);

  const submitable = !!friends.length > 0;

  return(
    <PageFade current={8}>
      <section id="nineth-page">
        <div className="container h-100 px-64 footer-border bg-white">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">Will you travel with<br/>someone?</h2>
            {friends.length > 0 && 
              <Scrollbar data={friends} title="companion" />}
            
            <div className="form-groups">
              <form className="flight-box" id="flight-0">
                <div className="flight-box__wrappper">
                  <ValidTextInput
                    placeholder="Companion’s Name"
                    id="friend"
                    value={friend}
                    setValue={setFriend}
                    error={!submitable && friendMsg}
                  />
                </div>
                <div className="flight-box__wrapper ml-tw-24 mt-tw-20">
                  <ValidTextInput
                    placeholder="Age"
                    id="age"
                    value={age}
                    setValue={setAge}
                    error={(!submitable || ageMsg === 'Age must be number!') && ageMsg}
                  />
                </div>
                <div className="btn btn-add btn-flight">
                  <span className="status">+</span>
                  <button className="btn-add" disabled={!done} onClick={onAddItem}>Add a companion</button>
                </div>
              </form>
            </div>            
            <div className="button-group">
              <button className="btn btn-white" 
                onClick={() => onOnlyMe()}
              >
                ITS ONLY ME
              </button>
              <NextButton label="Next" disabled={!submitable} where={9} current={8} />
            </div>
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default NinethPage;