import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';

import FirstPage from './first-page';
import SecondPage from './second-page';
import ThirdPage from './third-page';
import FourthPage from './fourth-page'
import FifthPage from './fifth-page'
import SixthPage from './sixth-page';
import SeventhPage from './seventh-page';
import EighthPage from './eighth-page';
import NinethPage from './nineth-page';
import TenthPage from './tenth-page';
import EleventhPage from './eleventh-page';
import TwelvethPage from './twelveth-page';
import ThirteenthPage from './thirteen-page';
import FourteenthPage from './fourteenth-page'

const MainPage = () => {
  const {pageID} = useSelector(store => store.page);
  const page = useCallback(() => {
    switch(pageID) {
      case 13: return <FourteenthPage/>
      case 12: return <ThirteenthPage/>
      case 11: return <TwelvethPage/>
      case 10: return <EleventhPage/>
      case 9: return <TenthPage/>
      case 8: return <NinethPage/>
      case 7: return <EighthPage/>
      case 6: return <SeventhPage/>
      case 5: return <SixthPage/>
      case 4: return <FifthPage/>
      case 3: return <FourthPage/>
      case 2: return <ThirdPage/>
      case 1: return <SecondPage/>;
      case 0:
      default: return <FirstPage/>;
    }
  }, [pageID])
  return (
    <React.Fragment>
      {page()}
    </React.Fragment>
  )
}

export default MainPage;