import React from 'react';
import { useSelector } from 'react-redux';
import Fade from '../components/fade';

const PageFade = ({current, children}) => {
  const [inProp, setInProp] = React.useState(false);
  const {pageID} = useSelector(store => store.page);
  React.useEffect(() => {
    setInProp(pageID === current);
  }, [pageID, current])
  return (
    <Fade in={inProp}>
      {children}
    </Fade>
  )
}

export default PageFade;