import React, { useEffect, useState, useCallback, Fragment } from 'react';
import '../assets/scss/components/credit-card.scss';

import SelectDropdown from '../components/form/selectDropDown';
import Visa from '../assets/img/visa.png';
import Mastercard from '../assets/img/mastercard.png';

const CURRENT_YEAR = new Date().getFullYear();
const CURRENT_MONTH = new Date().getMonth();

const monArr = ['JAN', 'FEB', 'MAR', 'APR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DEC'];
let MONTHS = {},
  YEARS = [CURRENT_YEAR];
for (let i = 1; i <= 12; i++) {
  MONTHS[i] = i.toString().length === 1 ? `0${i}` : i.toString();
  YEARS.push(YEARS[0] + i);
}
const CreditCard = props => {
  const {setValue, value} = props;
  const [cardNumber, setCardNumber] = useState(value.cardNumber);
  const [cardName, setCardName] = useState(value.cardName);
  const [cardCvv, setCardCvv] = useState(value.cardCvv);
  const [cardType, setCardType] = useState(value.cardType);
  const [cardYear, setCardYear] = useState(value.cardYear);
  const [cardMonth, setCardMonth] = useState(value.cardMonth);
  
  console.log(cardMonth)
  const onChangeMonth = useCallback( value => {
    setCardMonth(monArr.indexOf(value) + 1)
  }, [])
  
  const onChangeYear = useCallback( value => {
    value = Number(value);
    if (value === CURRENT_YEAR && cardMonth <= CURRENT_MONTH) {
      setCardMonth(0);
      setCardYear(value);
    } else {
      setCardYear(value);
    }
  }, [cardMonth]);
  
  const onCardType = useCallback( type => {
    setCardType(type);
  }, [])
  
  const handleChange = useCallback((event, type) => {
    let { value } = event.target;
    if (type === "cardNumber") {
      value = value.replace(/-/gi, "");
      if (isNaN(value)) {
        return;
      } else {
        setCardNumber(value);
      }
    } else if (type === "cardName") {
      var regName = /^[a-zA-Z\s]*$/;
      if (!regName.test(value)) {
      } else {
        setCardName(value);
      }
    } else if (type === "cardCvv") {
      value = value.replace(/ /gi, "");
      setCardCvv(value);
    }
  }, []);

  useEffect(() => {
    setValue({
      cardNumber,
      cardName,
      cardMonth,
      cardYear,
      cardCvv,
      cardType
    })
  }, [cardName, cardNumber, cardMonth, cardYear, cardCvv, cardType, setValue]);

  const formatCardNumber = useCallback( value => {
    let v = value.replace(/\s+/g, "").replace(/[^0-9]/gi, "");
    let matches = v.match(/\d{4,16}/g);
    let match = (matches && matches[0]) || "";
    let parts = [];
    for (let i = 0, len = match.length; i < len; i += 4) {
      parts.push(match.substring(i, i + 4));
    }
    if (parts.length) {
      return parts.join("-");
    } else {
      return value;
    }
  }, [])

  const month = useCallback(() => {
    const array = [];
    for (let i = 1; i <= 12; i++) {
      array.push(monArr[i - 1]);
    }
    return array;
  }, []);

  const year = useCallback(() => {
    const array = [];
    for (let i = 0; i < 10; i++) {
      array.push(CURRENT_YEAR + i);
    }
    return array;
  }, []);

  return (
    <Fragment>
      <div className="card-input">
        <label htmlFor="cardName">Card holder name</label>
        <input
          className="name-input"
          id="cardName"
          type="text"
          onChange={event => handleChange(event, "cardName")}
          value={cardName}
          maxLength="24"
        />
      </div>
      <div className="card-input card-number">
        <label htmlFor="cardNumber">Card number</label>
        <input
          className="number-input"
          id="cardNumber"
          type="text"
          onChange={event => handleChange(event, "cardNumber")}
          value={formatCardNumber(cardNumber)}
          maxLength="19"
        />
        <div className="credit-cards">
          <img 
            src={Mastercard}
            alt="Mastercard"
            className={ cardType === 'mastercard' ? 'mastercard' : 'mastercard disable'}
            onClick={()=>onCardType('mastercard')}
          />
          <img
            src={Visa}
            alt="Visa"
            className={ cardType === 'visa' ? 'visa' : 'visa disable'}
            onClick={()=>onCardType('visa')}
          />
        </div>
      </div>
      <div className="med-input">
        <label htmlFor="cardMonth">Expires</label>
        <SelectDropdown key="credit-month"
          id="month"
          pClass="input-month"
          data={month()}
          placeholder='MM'
          value={cardMonth === 0 ? '' : monArr[cardMonth - 1] }
          setValue={onChangeMonth}
          disNum={cardYear === CURRENT_YEAR ? CURRENT_MONTH - 1 : -1}
        />
        <SelectDropdown key="credit-year"
          id="year"
          pClass="input-year"
          data={year()}
          placeholder='YYYY'
          value={cardYear}
          setValue={onChangeYear}
        />
        <div className="sm-input">
          <label htmlFor="cardCvv">CVC</label>
          <input
            className="cvv-input"
            id="cardCvv"
            value={cardCvv}
            onChange={event => handleChange(event, "cardCvv")}
            maxLength="3"
          />
        </div>
      </div>
    </Fragment>
  )
}

export default CreditCard;