import React, {useState} from 'react';
import { useSelector } from 'react-redux';
import '../assets/scss/pages/_sixth-page.scss'

import PageFade from '../components/page-fade';
import NextButton from '../components/next-button';
import DatePicker from 'react-date-picker';
import * as Actions from '../store/action'

const WEEK_DAYS = ['S', 'M', 'T', 'W', 'T', 'F', 'S'];
const MONTH_LIST = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

const CalenderNavLabel = (props) => {
  const { date } = props;
  return (
    <span>{MONTH_LIST[date.getMonth()]}</span>
  )
}

const SixthPage = () => {
  const {dateList} = useSelector(store => store.user);
  const [start, setStart] = useState(dateList.start || '');
  const [end, setEnd] = useState(dateList.end || '');

  const submitable = React.useMemo(() => {
    return start !== '' && end !== ''
  }, [start, end]);

  return(
    <PageFade current={5}>
     <section id="sixth-page">
        <div className="main container h-100 px-64 footer-border">
          <div className="centered-box z-1 top-little full-width text-center">
            <h2 className="text-primary">When are you <br/>traveling?</h2>
            <form>
              <DatePicker value={start} onChange={setStart}
                monthPlaceholder="MM" dayPlaceholder="DD"
                yearPlaceholder="YYYY" format="dd/MM/y"
                required className="datepikcer"
                formatShortWeekday={(locale, date) => {
                  const day = date.getDay()
                  return WEEK_DAYS[day]
                }}
                calendarType='US'
                navigationLabel={CalenderNavLabel}
                
              />
              <DatePicker value={end} onChange={setEnd}
                monthPlaceholder="MM" dayPlaceholder="DD"
                yearPlaceholder="YYYY" format="dd/MM/y"
                required className="datepikcer ml-tw-24 mt-tw-20"
                formatShortWeekday={(locale, date) => {
                  const day = date.getDay()
                  return WEEK_DAYS[day]
                }}
                calendarType='US'
                navigationLabel={CalenderNavLabel}
              />
            </form>
            <NextButton label="Next"
              disabled={!submitable}
              where={8}
              current={5}
              data={{
                type: Actions.CHANGE_DATE,
                data: {
                  start,
                  end
                }
              }}
            />
          </div>
        </div>
      </section>
    </PageFade>
  )
}

export default SixthPage;